.app {
  margin: 0 auto;
  max-width: 940px;

  &__header {
    margin-bottom: 20px;
  }

  &__title {
    text-align: center;
  }

  &__goods {
    display: flex;
    justify-content: center;
    gap: 20px;
  }

  &__list {
    width: 90%;
    margin: 0 auto;
  }

  &__list-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 12px;
    padding: 16px;

    border-radius: 3px;
    background-color: #fefefe;
    box-shadow: $box-shadow-default;

    &--selected {
      background-color: #a0effa;
    }
  }

  &__button {
    padding: 10px;
    border-radius: 4px;

    cursor: pointer;

    transition: box-shadow 0.3s linear;

    &:hover {
      box-shadow: none;
    }

    &--add {
      background-color: #8c658f;
      box-shadow: 0 3px 0 0 #624764;
    }

    &--remove {
      background-color: #f34d93;
      box-shadow: 0 3px 0 0 #aa3667;
    }
  }

}
